const cloudFunction = async (url, data, err) => {
    try {
      let res = await qq.BaaS.invokeFunction(url, data)
      return [null, res]
    } catch (e) {
      return [e, null]
    }
  }
  
  const cloudRequest = async (url, data, err) => {
      try {
        let res = await qq.BaaS.invokeFunction({
          url,
          data
        })
        return [null, res]
      } catch (e) {
        return [e, null]
      }
    }
  
  export { cloudRequest, cloudFunction}
  