import {
  SET_USERINFO,
  SET_ISAUTH,
  SET_ISLOGIN,
  SET_DEVICE,
  SET_HASBINDCLUB,
  SET_HASBINDSCHOOL,
  SET_HASUSERINFO,
  SET_CLUB,
  SET_ISIPX
} from './../mutations_type'

const state = {
  userInfo: {},
  club: {},
  isAuth: false,
  isLogin: false,
  isIpx: false,
  hasUserInfo: false,
  hasBindClub: false,
  hasBindSchool: false,
  device: {}
}

const mutations = {
  [SET_USERINFO](state, data) {
    state.userInfo = data
  },
  [SET_CLUB](state, data) {
    state.club = data
  },
  [SET_ISAUTH](state, data) {
    state.isAuth = data
  },
  [SET_HASBINDCLUB](state, data) {
    state.hasBindClub = data
  },
  [SET_HASBINDSCHOOL](state, data) {
    state.hasBindSchool = data
  },
  [SET_HASUSERINFO](state, data) {
    state.hasUserInfo = data
  },
  [SET_ISLOGIN](state, data) {
    state.isLogin = data
  },
  [SET_DEVICE](state, data) {
    state.device = data
  },
  [SET_ISIPX](state, data) {
    state.isIpx = data
  }
}

const getters = {
  userInfo: state => {
    return state.userInfo
  },
  club: state => {
    return state.club
  },
  isAuth: state => {
    return state.isAuth
  },
  hasBindClub: state => {
    return state.hasBindClub
  },
  hasBindSchool: state => {
    return state.hasBindSchool
  },
  hasUserInfo: state => {
    return state.hasUserInfo
  },
  isLogin: state => {
    return state.isLogin
  },
  isIpx: state => {
    return state.isIpx
  }
}

const actions = {
  // 获取用户信息
  async getUserInfo({ commit }) {
    try {
      let user = await qq.BaaS.auth.loginWithQQ()
      // user 为 currentUser 对象
      // 已经绑定学校
      commit(SET_USERINFO, {
        nickname: user.nickname,
        avatar: user.avatar,
        userId: user.user_id
      })
      commit(SET_ISAUTH, user.is_authorized || false)
      commit(SET_HASUSERINFO, true)
      return true
    } catch (err) {
      console.log('getUserInfo', err)
      commit(SET_ISAUTH, false)
      return false
    }
  },

  // 微信用户授权
  async authUserInfo({ commit }, userInfo) {
    const res = await qq.BaaS.auth.loginWithQQ(userInfo)
    commit(SET_USERINFO, {
      nickname: res.nickname,
      avatar: res.avatar,
      userId: res.user_id
    })
    commit(SET_ISAUTH, true)
    return true
  },

  // 设置设备信息
  getSystemInfo({ commit }) {
    try {
      const res = qq.getSystemInfoSync()
      commit(SET_ISIPX, res.model.includes('iPhone'))
    } catch (err) {
      console.log(err)
    }
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}
